// @ts-nocheck
import React, { useEffect, useState, useContext } from 'react'
import { Button, Avatar, Card, Select, MenuItem, TextField } from "@material-ui/core";
import "./profile.css";
import { useSnackbar } from "notistack";
import { Timeline } from 'react-twitter-widgets'
import { FirebaseContext } from "../firebaseContext";
import { LinkPreview } from '@dhaiwat10/react-link-preview';
import { UserCard } from 'react-github-cards/dist/medium'
import 'react-github-cards/dist/medium.css';

export const Profile = (
    { address, account }
) => {
    const { enqueueSnackbar } = useSnackbar();
    const { getModules, setModule, getObjkts } = useContext(FirebaseContext);
    const [username, setUsername] = useState(address.slice(0, 6) + "..." + address.slice(32, 36));
    const isMobile = window.innerWidth <= 500;
    const [editMode, setEditMode] = useState(false);
    const [selectedEditField, setSelectedEditField] = React.useState("twitter");
    const inputLabels = {
        "twitter": "adventurecorpp",
        "tumblr": "tezos",
        "github": "tanerdurmaz",
        "alias": "nickname",
        "ethereum wallet": "0x4bA9285EDE9D46Bc367ee1d5cD5a8F1d9e6572e6",
        "website": "https://adventurenetworks.net/"
    };
    const [input, setInput] = useState();

    //profile modules
    const [twitter, setTwitter] = useState();
    const [tumblr, setTumblr] = useState();
    const [github, setGithub] = useState();
    const [alias, setAlias] = useState();
    const [ethereum, setEthereum] = useState();
    const [createdObjkts, setCreatedObjkts] = useState();
    const [website, setWebsite] = useState();

    useEffect(() => {
        async function fetchModules() {
            let modules = await getModules(address);
            modules.forEach(module => {
                if (module.type === "twitter") {
                    setTwitter(module.value);
                } else if (module.type === "tumblr") {
                    setTumblr(module.value);
                } else if (module.type === "github") {
                    setGithub(module.value);
                }
                else if (module.type === "alias") {
                    setAlias(module.value);
                }
                else if (module.type === "ethereum") {
                    setEthereum(module.value);
                }
                else if (module.type === "website") {
                    setWebsite(module.value);
                }
            });
        }

        fetchModules();
    }, [getModules, setModule, address]);

    function changeProfile() {
        //set
        //notify
        if (selectedEditField === "twitter") {
            setModule(address, "twitter", input);
            setTwitter(input);
        } else if (selectedEditField === "tumblr") {
            setModule(address, "tumblr", input);
            setTumblr(input);
        } else if (selectedEditField === "github") {
            setModule(address, "github", input);
            setGithub(input);
        } else if (selectedEditField === "alias") {
            setModule(address, "alias", input);
            setAlias(input);
        } else if (selectedEditField === "ethereum") {
            setModule(address, "ethereum", input);
            setEthereum(input);
        } else if (selectedEditField === "website") {
            setModule(address, "website", input);
            setWebsite(input);
        }
    }

    const handleChangeInput = (event: React.ChangeEvent<HTMLInputElement>) => {
        setInput(event.target.value)
    };

    const handleKeyPress = (event) => {
        if (event.key === "Enter") {
            changeProfile()
        }
    };

    const handleChange = (event: SelectChangeEvent) => {
        setSelectedEditField(event.target.value as string);

        if (event.target.value === "twitter" && twitter) {
            setInput(twitter)
        } else if (event.target.value === "tumblr" && tumblr) {
            setInput(tumblr)
        } else if (event.target.value === "github" && github) {
            setInput(github)
        } else if (event.target.value === "alias" && alias) {
            setInput(alias)
        } else if (event.target.value === "ethereum" && ethereum) {
            setInput(ethereum)
        } else if (event.target.value === "website" && website) {
            setInput(website)
        }
        else {
            setInput(inputLabels[event.target.value])
        }
    };

    useEffect(() => {
        //get wallet names for ex. taner.tez
        async function getDomain(address: string) {
            let domain;
            await fetch('https://api.tezos.domains/graphql', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    query: `
            {
              reverseRecord(address: "`+ address + `"){owner domain{name}}
            }
            `,
                    variables: {
                    },
                }
                ),
            })
                .then((res) => res.json())
                .then((result) => {
                    console.log(result);
                    if (result.data.reverseRecord) {
                        domain = result.data.reverseRecord.domain.name;
                        setUsername(domain);
                    }
                });
        }
        getDomain(address)
    }, [address]);

    useEffect(() => {
        async function fetchGraphQL(operationsDoc, operationName, variables, indexer) {
            let result = await fetch(indexer, {
                method: 'POST',
                body: JSON.stringify({
                    query: operationsDoc,
                    variables: variables,
                    operationName: operationName,
                }),
            })

            var ress = await result.json();
            return ress;
        }

        async function fetchTezosProfiles(addr) {

            const { errors, data } = await fetchGraphQL(
                query_profile,
                'TezosProfiles',
                { address: addr },
                'https://indexer.tzprofiles.com/v1/graphql'
            )
            if (errors) {
                console.error(errors)
            }
            if (data) {
                console.log(data);

                if (data.tzprofiles_by_pk) {
                    if (!twitter && data.tzprofiles_by_pk.twitter) {
                        setTwitter(data.tzprofiles_by_pk.twitter);
                    }
                    if (!github && data.tzprofiles_by_pk.github) {
                        setGithub(data.tzprofiles_by_pk.github)
                    }
                    if (!alias && data.tzprofiles_by_pk.alias) {
                        setAlias(data.tzprofiles_by_pk.alias)
                    }
                    if (!ethereum && data.tzprofiles_by_pk.ethereum) {
                        setEthereum(data.tzprofiles_by_pk.ethereum)
                    }
                    if (!website && data.tzprofiles_by_pk.website) {
                        setWebsite(data.tzprofiles_by_pk.website)
                    }
                }
            }
        }
        fetchTezosProfiles(address)

        async function fetchObjkts(address) {
            setCreatedObjkts((await getObjkts(address)).token);
        }
        fetchObjkts(address);
    }, [address, alias, ethereum, github, twitter, website]);



    return (
        <div >
            <div className="center">
                <Avatar
                    alt="Profile Picture"
                    src={`https://services.tzkt.io/v1/avatars/${address}`}
                    style={{ width: 150, height: 150 }}
                />
            </div>
            <div className="center">
                <b>{alias}</b>
            </div>
            <div className="center">
                <Button
                    onClick={() => {
                        navigator.clipboard.writeText(address);
                        enqueueSnackbar(
                            <div onClick={() => {
                                window.open(`https://tzkt.io/${address}/operations`);
                            }}
                            > Wallet address coppied, click to view on explorer </div>, {
                            variant: "success",
                        });
                    }}
                >{username}</Button>
            </div>
            {account && account.address === address &&
                <div className="center" style={{ marginBottom: 20 }}>
                    <Button
                        variant="contained" color="primary"
                        onClick={() => { setEditMode(!editMode) }}
                    >
                        {editMode ? "finish edit" : "edit"}
                    </Button>

                </div>
            }

            {!editMode &&
                <div className="modules" style={{ display: isMobile ? "grid" : "flex", width: "100vw", flexWrap: "wrap" }}>

                    {twitter &&
                        <div className="twitter" style={{ marginInline: isMobile ? "auto" : 10, marginBlock: isMobile ? 10 : 0, width: isMobile ? "90%" : "24%" }}>
                            <Card variant="outlined">
                                <Timeline
                                    dataSource={{
                                        sourceType: 'profile',
                                        screenName: twitter
                                    }}
                                    options={{
                                        width: '100%',
                                        height: '600'
                                    }}
                                />
                            </Card>
                        </div>
                    }

                    {tumblr &&
                        <div className="tumblr" style={{ marginInline: isMobile ? "auto" : 10, marginBlock: isMobile ? 10 : 0, width: isMobile ? "90%" : "24%" }}>
                            <Card variant="outlined" >
                                <iframe title={"tumblr"} src={`https://${tumblr}.tumblr.com/`} width={"100%"} height={600}></iframe>
                            </Card>
                        </div>
                    }



                    {ethereum &&
                        <div style={{ marginInline: isMobile ? "auto" : 10, marginBlock: isMobile ? 10 : 0, width: isMobile ? "90%" : "24%" }}>
                            <Card variant="outlined" >
                                <iframe src={`https://opensea.io/${ethereum}?embed=true`}
                                    title="opensea"
                                    width='100%'
                                    height='600'
                                ></iframe>
                            </Card>
                        </div>
                    }



                    <div style={{ width: isMobile ? "90%" : "24%", height: 600, overflow: "scroll" }}>
                        <Card variant="outlined" >
                            <div style={{ textAlign: "center" }}> <h2>Created Objkts</h2></div>

                            {createdObjkts && createdObjkts.map((token, index) => (
                                <div style={{ marginTop: 10 }}>
                                    <Card variant="outlined" >
                                        <div style={{ textAlign: "center", fontSize: "1.1em" }}> <b>{token.title}</b></div>
                                        <img src={HashToURL(token.display_uri, 'IPFS')} alt={token.title} width="100%" />
                                        <div style={{ textAlign: "center" }}> <b>{token.supply} X </b></div>
                                        <div style={{ display: "flex" }}>
                                            <div>
                                                <b>
                                                    price: {token.lowest_ask ? ((token.lowest_ask / 1000000) + "ꜩ") : "-"}
                                                </b>
                                            </div>
                                            <div style={{ marginLeft: "auto" }}>
                                                <b>
                                                    offer: {token.highest_bid ? ((token.highest_bid / 1000000) + "ꜩ") : "-"}
                                                </b>
                                            </div>
                                        </div>
                                    </Card>
                                </div>
                            ))}
                        </Card>
                    </div>

                    {website &&
                        <div style={{ marginInline: isMobile ? "auto" : 10, marginBlock: isMobile ? 10 : 0, width: isMobile ? "90%" : "24%" }}>
                            <LinkPreview
                                url={website}
                                //fallback={noLinkPrev}
                                descriptionLength={50}
                                imageHeight={300}
                                showLoader={true}
                            />
                        </div>
                    }
                    {github &&
                        <div className="github" style={{ width: isMobile ? "90%" : "24%", height: 260, marginInline: isMobile ? "auto" : 10, marginBlock: isMobile ? 10 : 0 }}>
                            <div style={{ justifyContent: "center", width: "100%", display: "flex" }}>
                                <Card variant="outlined" >
                                    <UserCard username={github} />
                                </Card>
                            </div>
                        </div>
                    }
                </div>
            }

            {editMode &&
                <div style={{ marginTop: 30 }}>
                    <div className="center">
                        <Select
                            variant="outlined"
                            value={selectedEditField}
                            onChange={handleChange}
                        >
                            <MenuItem value={"twitter"}>Twitter</MenuItem>
                            <MenuItem value={"tumblr"}>Tumblr</MenuItem>
                            <MenuItem value={"github"}>Github</MenuItem>
                            <MenuItem value={"alias"}>Alias</MenuItem>
                            <MenuItem value={"ethereum"}>Ethereum</MenuItem>
                            <MenuItem value={"website"}>Website</MenuItem>
                        </Select>

                        <TextField id="outlined-basic" label={inputLabels[selectedEditField]} variant="outlined"
                            onChange={handleChangeInput}
                            value={input}
                            onKeyPress={handleKeyPress}
                        />

                        <Button
                            variant="contained" color="primary"
                            style={{ marginLeft: 10 }}
                            onClick={() => { changeProfile() }}
                        >
                            Post
                        </Button>
                    </div>
                    <div className="center" style={{ marginTop: 25 }}><b>auto preview</b> </div>
                    <div className="center" style={{ marginTop: 5 }}>
                        {selectedEditField === "twitter" &&
                            <div className="twitter" style={{ marginInline: isMobile ? "auto" : 10, marginBlock: isMobile ? 10 : 0 }}>
                                <Card variant="outlined">
                                    <Timeline
                                        dataSource={{
                                            sourceType: 'profile',
                                            screenName: input
                                        }}
                                        options={{
                                            width: '400',
                                            height: '600'
                                        }}
                                    />
                                </Card>
                            </div>
                        }

                        {selectedEditField === "tumblr" &&
                            <div className="tumblr" >
                                <Card variant="outlined" >
                                    <iframe title={"tumblr"} src={`https://${input}.tumblr.com/`} width={400} height={600}></iframe>
                                </Card>
                            </div>
                        }

                        {selectedEditField === "github" &&
                            <div className="github" style={{ height: 260, marginInline: isMobile ? "auto" : 10, marginBlock: isMobile ? 10 : 0 }}>
                                <Card variant="outlined" >
                                    <UserCard key={input} username={input} />
                                </Card>
                            </div>
                        }

                        {selectedEditField === "alias" &&
                            <div style={{ marginTop: 25 }}>
                                <b>{alias}</b>
                            </div>
                        }
                        {selectedEditField === "ethereum" && input &&
                            <Card variant="outlined" >
                                <iframe src={`https://opensea.io/${input}?embed=true`}
                                    title="opensea"
                                    width='400'
                                    height='600'

                                ></iframe>
                            </Card>
                        }
                        {selectedEditField === "website" &&
                            <LinkPreview

                                url={input}
                                //fallback={noLinkPrev}
                                descriptionLength={50}
                                imageHeight={300}
                                showLoader={true}
                            />
                        }
                    </div>
                    <div className="center" style={{ marginTop: 25 }}><b>Links</b> </div>

                    <div className="center" style={{ marginTop: 25 }}>
                        <table style={{ width: 400 }}>

                            {twitter && <tr>
                                <td >Twitter</td>
                                <td>{twitter}</td>
                                <td>
                                    <Button
                                        variant="contained" color="primary"
                                        style={{ marginLeft: 10 }}
                                        onClick={() => {
                                            setModule(address, "twitter", "");
                                            setTwitter("");
                                        }}
                                    >
                                        X
                                    </Button>
                                </td>
                            </tr>
                            }
                            {tumblr && <tr>
                                <td >Tumblr</td>
                                <td>{tumblr}</td>
                                <td>
                                    <Button
                                        variant="contained" color="primary"
                                        style={{ marginLeft: 10 }}
                                        onClick={() => {
                                            setModule(address, "tumblr", "");
                                            setTumblr("");
                                        }}
                                    >
                                        X
                                    </Button>
                                </td>
                            </tr>
                            }
                            {github && <tr>
                                <td >Github</td>
                                <td>{github}</td>
                                <td>
                                    <Button
                                        variant="contained" color="primary"
                                        style={{ marginLeft: 10 }}
                                        onClick={() => {
                                            setModule(address, "github", "");
                                            setGithub("");
                                        }}
                                    >
                                        X
                                    </Button>
                                </td>
                            </tr>}
                            {alias && <tr>
                                <td >Alias</td>
                                <td>{alias}</td>
                                <td>
                                    <Button
                                        variant="contained" color="primary"
                                        style={{ marginLeft: 10 }}
                                        onClick={() => {
                                            setModule(address, "alias", "");
                                            setAlias("");
                                        }}
                                    >
                                        X
                                    </Button>
                                </td>
                            </tr>}
                            {ethereum && <tr>
                                <td >Ethereum</td>
                                <td>{ethereum}</td>
                                <td>
                                    <Button
                                        variant="contained" color="primary"
                                        style={{ marginLeft: 10 }}
                                        onClick={() => {
                                            setModule(address, "ethereum", "");
                                            setEthereum("");
                                        }}
                                    >
                                        X
                                    </Button>
                                </td>
                            </tr>}
                            {website && <tr>
                                <td >Website</td>
                                <td>{website}</td>
                                <td>
                                    <Button
                                        variant="contained" color="primary"
                                        style={{ marginLeft: 10 }}
                                        onClick={() => {
                                            setModule(address, "website", "");
                                            setWebsite("");
                                        }}
                                    >
                                        X
                                    </Button>
                                </td>
                            </tr>}
                        </table>

                    </div>






                </div>
            }
        </div>
    );

};

const query_profile = `
query TezosProfiles($address: String!){ 
    tzprofiles_by_pk(account: $address) 
    { 
      account,
      discord,
      twitter,
      github,
      ethereum,
      domain_name,
      logo,
  alias,
      website,
      description,
          valid_claims,
  
    } 
  }
`


const HashToURL = (hash, type) => {
    // when on preview the hash might be undefined.
    // its safe to return empty string as whatever called HashToURL is not going to be used
    // artifactUri or displayUri
    if (hash === undefined) {
        return ''
    }

    switch (type) {
        case 'HIC':
            return hash.replace('ipfs://', 'https://pinata.hicetnunc.xyz/ipfs/')
        case 'CLOUDFLARE':
            return hash.replace('ipfs://', 'https://cloudflare-ipfs.com/ipfs/')
        case 'PINATA':
            return hash.replace('ipfs://', 'https://gateway.pinata.cloud/ipfs/')
        case 'IPFS':
            return hash.replace('ipfs://', 'https://ipfs.io/ipfs/')
        case 'INFURA':
            try {
                var cidv1 = new ipfsClient.CID(hash.replace('ipfs://', '')).toV1()
                var subdomain = cidv1.toBaseEncodedString('base32')
                return `https://${subdomain}.ipfs.infura-ipfs.io/`
            } catch (err) {
                return undefined
            }
        case 'DWEB':
            return hash.replace('ipfs://', 'http://dweb.link/ipfs/')
        default:
            console.error('please specify type')
            return hash
    }
}